@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-arrow-right {
    width: (20 / 14) * 1em;
}
.icon-close {
    width: (26 / 25) * 1em;
}
.icon-phone {
    width: (27 / 25) * 1em;
}
.icon-play {
    width: (15 / 18) * 1em;
}
.icon-telegram {
    width: (24 / 20) * 1em;
}
.icon-watsapp {
    width: (22 / 22) * 1em;
}
