.s-main-banner {
	position: relative;
	min-height: 100vh;
	color: #FFF;
	padding-top: 167px;
	padding-left: 20px;
	padding-right: 20px;
	//background-color: $turquoise2;
	@include media-breakpoint-up(md) {
		padding-top: 121px;
	}
	@include media-breakpoint-up(lg) {
		padding-left: (272/1440*100vw);
		padding-right: (152/1440*100vw);
		padding-top: 121px;
		min-height: calc(100vh - 100px);
	}

	@media (max-height: 800px) and (min-width: 992px) {
		padding-top: 80px;
	}
	&_main {
		padding-bottom: 120px;
		@include media-breakpoint-up(lg) {
			padding-top: 213px;
			padding-bottom: 222px;
		}
		@media (max-height: 800px) and (min-width: 992px) {
			padding-top: 80px;
		}
	}
	&:before {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 57.14%, rgba(0, 0, 0, 0.3) 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45));
		z-index: -1;
	}
	&__bg {
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		position: absolute;
		z-index: -2;
	}
	&__title {
		@include text-style-h1;
		&_large {
			margin:0;
			@include text-style-h1-large;
		}
	}
	&__desc {
		font-size: 18px;
		line-height: 1.4;
		font-family: $font-family-base;
		margin-top: 13px;
		@include media-breakpoint-up(lg) {
			font-size: 36px;
			line-height: 1.3;
		}
	}
	&__info-btm {
		margin-top: 50px;
		@include media-breakpoint-up(md) {
			position: absolute;
			bottom: 0;
			width: 314px;
		}
		&-top {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			@include text-style-middle;
			@include media-breakpoint-up(md) {
				flex-flow: row nowrap;
				justify-content: space-between;
			}
		}
		&-lnk {
			color: inherit;
			margin-top: 20px;
		}
		&-btn {
			margin-top: 30px;
		}
	}

	&__next-section {
		@include media-breakpoint-down(lg) {
			@include media-breakpoint-up(lg) {
			    left: 60%;
			}
		}
	}

	&__line {
		display: none;
		align-items: center;
		height: 100%;
		width: 100px;
		position: absolute;
		left: 0;
		top: 0;
		background-color: inherit;
		color: #000;
		z-index: 100;
		@include media-breakpoint-up(lg) {
			display: flex;
			justify-content: center;
		}
		&:after {
			content: '';
			width: 1px;
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			margin-top: percentage(40/700);
			height: percentage(5/7);
			background-color: rgba(#FFF, 0.2);
			@media (max-height: 800px) and (min-width: 992px) {
				height: 50%;
			}
		}
	}

	&__burger-menu {
		position: static;
		background-color: transparent;

	}
	&__social-group {
		display: none;
		@include media-breakpoint-up(lg) {
			display: flex;
			position: absolute;
			right: (40/1440*100vw);
			bottom: 35px;
		}
	}
}
