.s-about {
	@include media-breakpoint-up(lg) {
		padding-top: 30px;
	}
	&__row {
		@include media-breakpoint-up(md) {
			display: flex;
		}
		&_first {
			@include media-breakpoint-up(md) {
				margin-left: -20px;
			}
			@include media-breakpoint-up(lg) {
				margin-left: (-156/1440*100vw);
			}
		}
		&_two {
			@include media-breakpoint-up(md) {
				margin-right: -20px;
			}
			@include media-breakpoint-up(lg) {
				margin-right: (-40/1440*100vw);
			}
		}
	}
	&__col {
		&_video {
			margin: 0 -20px 40px;
			@include media-breakpoint-up(md) {
				margin: 0;
				max-width: percentage(821/1440);
				flex: 0 0 percentage(821/1440);
			}
		}
		&_pict{
			@include media-breakpoint-up(md) {
				margin: 0;
				max-width: percentage((550)/1440);
				flex: 0 0 percentage((550)/1440);
			}
		}
		&_desc-first {
			@include media-breakpoint-up(md) {
				padding-left: (40/1440*100vw);
				padding-bottom: 40px;
			}
			@include media-breakpoint-up(lg) {
				padding-left: ( 116/1440*100vw);
			}
		}
		&_desc-two {
			flex: 1;
			@include media-breakpoint-up(md) {
				padding-right: (40/1440*100vw);
				padding-top: 40px;
			}
			@include media-breakpoint-up(lg) {
				padding-top: 80px;
				padding-right: ( 126/1440*100vw);
				max-width: ( (433+126)/1440*100vw);
				flex: 0 0 ( (433+126)/1440*100vw);
				margin-left: auto;
			}
		}
	}
	&__video {
		display: block;
		border: none;
		position: relative;
		&-img {
			display: block;
			width: 100%;
		}
	}
	&__play {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 70px;
		height: 70px;
		border-radius: 100%;
		color: #FFF;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid currentColor;
		font-size: 10px;
		@include media-breakpoint-up(md) {
			font-size: 18px;
			width: 170px;
			height: 170px;
		}
	}
	&__title {
		margin-bottom: 20px;
		@include media-breakpoint-up(md) {
			margin-bottom: 40px;

		}
	}
	&__img {
		display: block;
		width: 100%;
	}
}
