.s-callback {
	&__row {
		@include media-breakpoint-up(md) {
			display: flex;
			justify-content: space-between;
		}
	}
	&__col {
		&_first {
			@include media-breakpoint-up(md) {
				max-width: percentage(531/1244);
				flex: 0 0 percentage(531/1244);
			}
		}
		&_two {
			margin-top: 20px;
			@include media-breakpoint-up(md) {
				margin-top: 0;
				max-width: percentage(433/1244);
				flex: 0 0 percentage(433/1244);
				margin-right: (120/1440*100vw);
			}
		}
	}
	&__desc {
		margin-top: 20px;
		@include media-breakpoint-up(lg) {
			margin-top: 40px;
		}
	}
}
