html {
	font-size:62.5%;
	height: 100%;
}

body {
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
	color: $font-base-color;
	@include base-text-style;
}
* {
	box-sizing: border-box;
}
.lnk,
a {
	text-decoration: none;
	transition: border-bottom-color $time-animation-base;
	border-bottom: 1px solid;
	color: $font-link-color;
	padding-bottom: 0.3em;
	&_border-brown {
		border-bottom-color: $brown;
	}
	&:hover,
	&:focus,
	&:active {
		text-decoration: none;
	}
	&:hover {
		border-bottom-color: transparent;
	}
}
.container {
	max-width: 1440px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 20px;
	padding-right: 20px;
	@include media-breakpoint-up(lg) {
		box-sizing: content-box;
		padding-left: (156/1440*100vw);
		padding-right: (40/1440*100vw);
	}
}
.s-section {
	padding-top: 40px;
	@include media-breakpoint-up(md) {
		padding-top: 90px;

	}
}
.next-section {
	position: absolute;
	bottom: 30px;
	left: 50%;
	transform: translateX(-50%);
	width: 50px;
	height: 50px;
	background-color: rgba(#FFF, 0.08);
	transition: background-color $time-animation-base;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100%;
	border: none;
	color: #FFF;
	cursor: pointer;
	@include media-breakpoint-up(md) {
		bottom: 35px;
	}
	&:hover {
		background-color: rgba(#FFF, 0.5);
	}
	&__icon {
			font-size: 14px;
			transform: rotate(90deg);
			transition: transform $time-animation-base;
		}
	&:hover &__icon {
		transform: rotate(90deg) translateX(5px);
	}
}
.burger-menu {
	position: fixed;
	bottom: 0;
	left: 0;
	top: calc(100vh - 100px);
	background-color: $turquoise2;
	cursor: pointer;
	border:none;
	width: 100px;
	height: 100px;
	color: #FFF;
	align-items: center;
	justify-content: center;
	transition: background-color $time-animation-base;
	display: none;
	z-index: 99;
	&:hover {
		background-color: darken($turquoise2, 5%);
	}
	&.is-active &__line {
		background: transparent;
	}
	&.is-active &__line:before {
		top: 0;
		transform: rotate(-45deg);
	}
	&.is-active &__line:after {
		bottom: 0;
		transform: rotate(45deg);
	}
	@include media-breakpoint-up(lg) {
		display: flex;
	}
	&__line {
		width: 30px;
		height: 4px;
		background:  currentColor;
		position: relative;
		transition: background-color $time-animation-base;
		&:after, &:before {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 100%;
			background:  #FFF;
			transition: top $time-animation-base, bottom $time-animation-base, transform $time-animation-base;
		}
		&:before {
			top: -10px;
		}
		&:after {
			bottom: -10px;
		}
	}
}
.social-group {
	display: flex;
	&__itm {

	}
	&__lnk {
		padding: 0;
		width: 50px;
		height: 50px;
		border: none;
		background-color: rgba(255, 255, 255, 0.08);
		transition: background-color $time-animation-base;
		color: #FFF;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 22px;
		border-radius: 100%;
		&:hover {
			background-color: rgba(255, 255, 255, 0.5);
		}
	}
}
.seporate {
	position: relative;
	border-bottom: 1px solid #000000;
	background-color: #FFF;
	height: 40px;
	margin-top: 40px;
	@include media-breakpoint-up(lg) {
		height: 80px;
		margin-top: 90px;
	}
	&:before {
		background-color: inherit;
		content: '';
		height: 40px;
		border-top: 1px solid #000000;
		border-right: 1px solid #000000;
		position: absolute;
		box-sizing: border-box;
		top: 0;
		left: 0;
		width: 208px;
		@include media-breakpoint-up(lg) {
			height: 80px;
			width: (392/1440*100vw);
		}
	}
	&:after {
		content: '';
		position: absolute;
		width: 207px;
		top: 1px;
		left: 0;
		background-color: #FFF;
		height: 45px;
		@include media-breakpoint-up(lg) {
			height: 85px;
			width: (391/1440*100vw);
		}
	}
	&_mt-0 {
		margin-top: 0;
	}
}
.hint {
	position: absolute;
	background: #FFFFFF;
	z-index: 10;
	padding: 15px;
	font-size: 10px;
	line-height: 1.4;
	border-radius: 10px;
	max-width: 208px;
	left: percentage(346/1440);
	top: percentage(334/800);
	@include media-breakpoint-up(md) {
		padding: 20px;
		max-width: 300px;
		font-size: 14px;
		border-radius: 15px;
	}
	&__close {
		position: absolute;
		top: 0;
		right: 0;
		color: #000000;
		padding: 5px;
		background-color: transparent;
		border: none;
		cursor: pointer;
		@include media-breakpoint-up(lg) {
			padding: 10px;
		}
	}
	&__icon {
		width: 14px;
		height: 14px;
		display: block;
	}
	&__ttl {
		font-weight: bold;
		font-size: 12px;
		@include media-breakpoint-up(md) {
			font-size: 18px;
		}
	}
	&__desc {
		padding-top: 8px;
		@include media-breakpoint-up(md) {
			padding-top: 15px;
		}
	}
	&.is-open {
		margin-top: -5px;
		@include media-breakpoint-up(lg) {
			margin-top: -10px;

		}
	}
	&:not(.is-open) {
		cursor: pointer;
		padding-top: 10px;
		padding-bottom: 10px;
		border-radius: 27px;
		@include media-breakpoint-up(lg) {
			border-radius: 40px;
		}
	}
	&:not(.is-open) &__ttl {
		font-weight: normal;
	}
	&:not(.is-open) &__close,
	&:not(.is-open) &__desc {
		display: none;
	}
}
