.btn {
	border: none;
	outline: none;
	box-shadow: none;
	cursor: pointer;
	font-family: $font-button-base;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	padding: 12px 20px;
	color: #FFF;
	font-size: 16px;
	line-height: 1.55 ;
	font-weight: bold;
	min-width: 255px;
	background-color: $brown;
	transition: background-color $time-animation-base;
	height: 60px;
	@include media-breakpoint-up(lg) {
		height: 80px;
	}
	&:hover {
		background-color: darken( $brown, 10%);
	}
	&:disabled,&[disabled], &:disabled:hover, &:disabled:focus {
		cursor: not-allowed;
	}
	&_fluid {
		width: 100%;
	}
	&_fluid-mobile {
		@include media-breakpoint-down(sm) {
			width: 100%;
		}
	}
	&_blue {
		background-color: $blue-light;
		&:hover {
			background-color: darken($blue-light, 10%);
		}
	}
	&__icon {
		margin-left: 15px;
		font-size: 14px;
	}
	&_sm {
		height: 60px;
		font-weight: 500;
		font-size: 18px;
		@include media-breakpoint-up(lg) {
			height: 70px;
			font-size: 18px;
		}
	}
	&_lg {
		height: 70px;
		font-weight: bold;
		font-size: 18px;
		@include media-breakpoint-up(lg) {
			height: 100px;
			font-size: 20px;
		}
	}
}
